@media screen and (max-width: 767px) {
  #Home .hero h1 {
    width: 100vw;
    top: 45vh;
  }
  #Home .hero .copyHero {
    width: 100vw;
    top: 65vh;
  }
  #Home .hero .buttonCTA {
    display: none;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  #Home .hero h1 {
    width: 35.75vw;
    height: 24vh;
    right: 0;
    top: calc(10vw + 5vh);
  }
  #Home .hero .copyHero {
    right: 0;
    top: calc(10vw + 5vh + 24vh);
    width: 35.75vw;
    height: 20vh;
  }
  #Home .hero .buttonCTA {
    display: flex;
    width: 35.75vw;
    right: 0;
    top: calc(10vw + 5vh + 24vh + 20vh);
    height: 7.25vh;
  }
  #Home .hero .buttonCTA button {
    width: 13.6552849022vw;
    height: 4.4807464184vh;
    font-size: 1.7114928369vh;
  }
}
