#Home .hero h1 {
  font-family: "Secular";
  color: #fc4f00;
  font-weight: 400;
  line-height: 2rem;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
#Home .hero .copyHero {
  font-family: Arial, Helvetica, Sans;
  font-size: 0.85rem;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
#Home .hero .buttonCTA {
  position: absolute;
  justify-content: center;
  align-items: center;
}
#Home .hero .buttonCTA a button {
  font-family: "Secular";
  background-color: #fc4f00;
  border-radius: 1vw;
  color: #e7e9eb;
}
#Home .hero .buttonCTA a :hover {
  color: #232424;
}