html {
  font-size: max(1rem, 2.472135955vh);
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  overflow-y: scroll;
  overflow-x: hidden;
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Secular";
  margin: 0;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  text-decoration: none;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.7639320236rem;
}

h3 {
  font-size: 1.3819660118rem;
}

h4, p {
  font-size: 1rem;
}

h5 {
  font-size: 0.6180339882rem;
}

h6 {
  font-size: 0.2360679764rem;
}