@media screen and (max-width: 767px) {
  footer {
    top: 0;
  }
  footer .nonMobileFooter {
    display: none;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  footer {
    bottom: 0;
  }
  footer .mobileFooter {
    display: none;
  }
}
/**@import './standard';
@import './hd1080';
@import './hd4kup';
*/
footer {
  position: fixed;
  left: 0;
  z-index: 1000000;
}
footer .footer {
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
}
footer p {
  line-height: 1rem;
  text-align: center;
  font-size: 0.5rem;
  color: #fff;
}