* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.parallax {
  height: 110vh;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.parallax:nth-last-of-type(1) {
  height: 110vh;
}

.p4 {
  width: 100vw;
  display: flex;
  background: #fc4f00;
}
.p4 form {
  z-index: 5;
  position: relative;
  text-align: center;
  display: flex;
  flex-flow: column;
}
.bodySection {
  color: #232424;
  text-align: center;
  width: 100vw;
  height: 110vh;
  padding: 0;
  margin: 0;
  position: absolute;
  left: 0;
}

.bodySection:nth-of-type(odd) {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: max(550px, 100vw);
}

.bodySection:nth-of-type(even) {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: max(550px, 100vw);
}

.bodySectionDk {
  color: #e7e9eb;
  text-align: center;
  width: 100vw;
  height: 110vh;
  padding: 0;
  margin: 0;
  position: absolute;
  left: 0;
}

.bodySectionDk:nth-of-type(odd) {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: max(550px, 100vw);
}

.bodySectionDk:nth-of-type(even) {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: max(550px, 100vw);
}