.topBox {
  position: fixed;
  left: 0;
  width: 100vw;
  height: 16vh;
  z-index: 1000;
}

.cta {
  width: 100vw;
  background-color: #fc4f00;
  color: #fff;
  font-family: "Secular";
  font-size: 1.5rem;
  text-align: center;
}

.header {
  width: 100vw;
  display: flex;
}
.header .logoBox {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .logoBox a picture img {
  margin-top: 0;
  margin-bottom: 0;
  max-width: 90%;
}
.header .mobileNavMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
}
.header .mobileNavMenu ul {
  list-style-type: none;
  position: absolute;
  z-index: 100000;
  background: #232424;
  line-height: 2rem;
  text-align: center;
  right: 0;
  font-family: "Secular";
  padding: 0;
  border: 1px solid rgba(35, 36, 36, 0.4);
  box-shadow: 0px 0px 5px #232424;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.header .mobileNavMenu ul li {
  height: 10vh;
  font-size: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .mobileNavMenu ul li a {
  text-decoration: none;
  color: #e7e9eb;
}
.header .mobileNavMenu ul li a:hover {
  color: #fc4f00;
}
.header .phone {
  display: flex;
  justify-content: center;
  position: absolute;
  height: 100%;
}
.header .phone a {
  text-decoration: none;
  color: #fc4f00;
  font-family: "Secular";
  background: rgba(231, 233, 235, 0.6);
  border-radius: 1vw;
  padding: 0 1vw;
}
.header .dkModeToggle {
  display: flex;
  justify-content: center;
  position: absolute;
  right: 0;
}
.header .dkModeToggle .lightMode {
  color: #e7e9eb;
  text-shadow: 0 0 calc(max(45px, 2vw) * 2.5 * 0.008) #232424;
  display: flex;
  align-items: center;
  text-align: right;
  padding: 0 calc(max(45px, 2vw) * 2.5 * 0.1);
}
.header .dkModeToggle .darkMode {
  color: #232424;
  text-shadow: 0 0 calc(max(45px, 2vw) * 2.5 * 0.008) #e7e9eb;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 0 calc(max(45px, 2vw) * 2.5 * 0.1);
}
.header .dkModeToggle label {
  width: calc(max(45px, 2vw) * 2.5);
  height: max(45px, 2vw);
  position: relative;
  display: block;
  background: #e7e9eb;
  border-radius: max(45px, 2vw);
  box-shadow: inset 0 calc(max(45px, 2vw) * 2.5 * 0.01) calc(max(45px, 2vw) * 2.5 * 0.03) rgba(35, 36, 36, 0.3960784314), inset 0 calc(max(45px, 2vw) * 2.5 * -0.01) calc(max(45px, 2vw) * 2.5 * 0.03) rgba(231, 233, 235, 0.3960784314);
  cursor: pointer;
  transition: 0.3s;
}
.header .dkModeToggle label:after {
  content: "";
  width: calc(max(45px, 2vw) * 2.5 * 0.36);
  height: calc(max(45px, 2vw) * 2.5 * 0.36);
  position: absolute;
  top: calc(max(45px, 2vw) * 2.5 * 0.02);
  left: calc(max(45px, 2vw) * 2.5 * 0.02);
  background: radial-gradient(circle at calc(max(45px, 2vw) * 2.5 * 0.18) calc(max(45px, 2vw) * 2.5 * 0.09), #ffcc89 10%, #fc4f00 70%);
  border-radius: calc(max(45px, 2vw) * 2.5 * 0.36);
  box-shadow: 0 calc(max(45px, 2vw) * 2.5 * 0.01) calc(max(45px, 2vw) * 2.5 * 0.02) rgba(35, 36, 36, 0.1960784314);
  transition: 0.3s;
}
.header .dkModeToggle label:active:after {
  width: calc(max(45px, 2vw) * 2.5 * 0.5);
}
.header .dkModeToggle input {
  width: 0;
  height: 0;
  visibility: hidden;
}
.header .dkModeToggle input:checked + label {
  background: rgba(35, 36, 36, 0.262745098);
}
.header .dkModeToggle input:checked + label:after {
  transform: translateX(-100%);
  background: radial-gradient(circle at calc(max(45px, 2vw) * 2.5 * 0.18) calc(max(45px, 2vw) * 2.5 * 0.09), rgba(255, 204, 137, 0.7333333333) 10%, rgba(252, 79, 0, 0.7333333333) 70%);
}